*{
    margin: 0;
    padding: 0;
}

.cabecera{
    position: relative;   
    background-color: black;
    border-bottom: 1px solid;
    border-color: black;
    margin: auto;
}

.logo{
    width: 100px;
    height: 100px;
    margin-left: 1%;
}

.botonesMenu{
    float: right;
    margin-top: 1.5%;
    /* margin-right: 3%; */
    width: 8%;
    height: 50px;
    color: white;
    background-color: black;
    border-color: black ;
}

.botonesMenu:hover{
    border-right-color: red;
    border-left-color: red;
}

.icono{
    margin-right: 2%;
}

.buscador{
    margin-right: 1%;
    color: white;
    border-width: 1px;
    border-color: white;
    border-radius: 10%;
    width: 20%;
}

.buscador:hover{
    border-color: red;
    border-width: 2px;
}